.app--header {
    text-align: center;
    margin: 10px 0 20px 0;
    color: rgb(114, 112, 112);
    font-weight: 500;
    text-transform: uppercase;
}

.main-container {
    /* padding: 5px; */
    margin: 10px auto;
    width: 98%;
    display: grid;
    grid-gap: 10px;
    padding-left: 10px;
    grid-template-rows: auto 1fr;
    grid-template-columns: 220px 85px 90px 90px 90px 160px 95px 70px 80px 90px 140px repeat(
            5,
            100px
        );
    background-color: rgb(245, 243, 243);
    background-color: #fff;
    color: rgb(109, 108, 108);
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    transition: all 0.4s ease-in-out;
    position: relative;
}

.item {
    border-radius: 5px;
    border: 1px solid #ccc;
    /* background-color: rgb(25, 114, 155); */
    /* color: aliceblue; */
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.item-0 {
    min-height: 30px;
}

.item-main {
    grid-column: 1 / -1;
    height: 35px;
    display: flex;
    justify-content: flex-start;
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    font-weight: 500;
    font-size: 12px;
    border: none;
    background-color: #85c3f0;
    background-color: #496ab8;
    background-color: #e9ecef;
    color: #ececec;
    color: rgb(49, 49, 49);
    /* background-image: linear-gradient(0deg, #2155cd 30%, #c0e1f8 90%); */
    position: relative;
    padding-left: 5px;
    transition: all 0.2s ease-in;
}

.item-main--open {
    background-color: #496ab8;
    color: #f8f8f8;
}

.item-0 {
    z-index: 1000;
}

.item-main span {
    position: sticky;
    left: 8px;
}

.subject-select {
    width: 50%;
    height: 40px;
    margin-left: 20px;
    font-size: 12px;
}

.sticky {
    position: sticky;
    left: 0;
    top: 0;
}

/******************** UTILITY CLASSES ***************************/

.bg--dark {
    background-color: rgb(25, 25, 112);
    background-color: #f4f7fc;
    font-weight: 500;
    color: #f1f1f1;
    color: #003459;
    padding: 5px 2px;
    position: sticky;
    /* left: 50%;
    top: 20%; */
}

.item-0 {
    background-color: rgb(247, 245, 245);
    background-color: #d8dbe0;
    /* color:#b61515; */
    justify-content: left;
    padding-left: 10px;
    font-weight: 500;
}

.item-edit {
    margin: 0 5px 0 -5px;
    padding: 4px 8px;
    cursor: pointer;
    color: #fff;
    background-color: #00a8e8;
    border: none;
    border-radius: 5px;
    transition: all 0.2s ease-out;
}

.item-edit:hover {
    background-color: #fff;
    color: #00a8e8;
}

/* #body {
    background-color: #f7f8f8;
    margin: 0 20px;
} */

.content {
    margin: 0 auto;
    margin-top: 20px;
    border-radius: 5px;
    padding: 10px;
    max-width: 1400px;
    background-color: #fff;
}

.content-form {
    padding-top: 20px;
}

.form-group {
    margin: 10px 0 10px 0;
}

.form-group span {
    color: rgb(160, 158, 158);
}

form {
    margin-left: 200px;
}

.edit--header {
    margin-left: 200px;
    margin-top: -10px;
    color: #496ab8;
}

.edit--return-button {
    border: 1px solid #1137df;
    padding: 4px 8px;
    position: absolute;
    top: 32px;
    font-size: 14px;
    background-color: #fff;
    border-radius: 3px;
    color: #1137df;
    transition: all 0.2s ease-in-out;
}

.fa-long-arrow-left {
    margin-right: 5px;
    transition: all 0.2s ease-in-out;
}

.edit--return-button:hover .fa-long-arrow-left {
    transform: translateX(-4px);
}

#body .form-group label {
    font-weight: 500;
    font-size: 14px;
    color: #000;
}

#body .form-control {
    /* border-color: rgb(64, 228, 173); */
    border: none;
    background-color: #e9ecef;
    border-radius: 8px;
    font-size: 12px;
}

#body .btn-save {
    padding: 6px 30px;
    font-size: 16px;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none !important;
    border: none;
    border-radius: 5px;
    box-shadow: none;
    background-color: rgb(65, 65, 65);
    color: #fff;
    transition: all 0.3s ease-in-out;
    transform: translate(0);
}

#body .btn-save:hover {
    background-color: rgb(0, 0, 0);
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
}

#body .btn-save:active {
    outline: none !important;
    border: none !important;
    border-color: none !important;
    transform: translateY(3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

#body .btn-add {
    background-color: crimson;
    color: #fff;
}

#body .btn-add:hover {
    background-color: rgb(163, 14, 44);
}

/* ***********************************  Utility Classes *********************************** */

.margin-left {
    margin-left: -40px;
}

#body .active {
    font-weight: 600;
    color: rgb(226, 10, 75);
}

.flex-div {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 10px;
}

.flex-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    margin-left: -40px;
}

/* *************************** Delete Icon **************************** */

.margin-bottom {
    margin-bottom: 10px !important;
}

.button-right {
    display: flex;
    justify-content: flex-end;
    padding: 0;
}

#body .btn-reset {
    padding: 6px 30px;
    font-size: 16px;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none;
    box-shadow: none;
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
    transform: translate(0);
    background-color: #fff;
    border: 1px solid #000;
    color: #000;
    margin-left: 10px;
}

#body .btn-reset:hover {
    background-color: rgb(65, 65, 65);
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgb(65, 65, 65);
    color: #fff;
    outline: none;
}

#body .btn-reset:active {
    outline: none;
    border-color: none;
    transform: translateY(3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.input {
    border: 1px solid #ccc;
    display: inline-block;
    font-family: inherit;
    min-width: 50px;
    font-size: inherit;
    padding: 1px 6px;
}

.auto {
    width: auto;
    position: relative !important;
}

.form-control {
    min-width: 150px !important;
    padding-right: 24px !important;
}

/* ******************* FILTER INPUTS *************************** */

.search-input {
    background-color: #f7f8f8;
    font-size: 12px;
    border: 1px solid rgb(64, 228, 173);
    border: 1px solid #86b7fe;
    border-radius: 5px;
    padding: 4px;
    margin-top: -4px;
    transition: all 0.2s ease-in;
}

.search-input:focus {
    border-color: #86b7fe;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px #86b7fe;
    outline: 0 none;
    background-color: #fff;
}

.btn-new {
    padding: 0 10px;
    margin-left: 10px;
    /* font-size: 20px; */
    font-family: inherit;
    letter-spacing: 1px;
    outline: none !important;
    border: none;
    border-radius: 5px;
    box-shadow: none;
    opacity: 0.6;
    color: #2155cd;
    background-color: #e7f1ff;
    transition: all 0.2s ease-in-out;
    transform: translate(0);
}

.btn-new:hover {
    background-color: #e7f1ff;
    opacity: 1;
    color: #2155cd;
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
}

.btn-new:active {
    outline: none !important;
    border: none !important;
    border-color: none !important;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.no-data {
    width: 90%;
    margin: 40px;
    padding: 20px;
    background-color: #f0f1f1;
}

.no-data--paragraph {
    margin-left: 15px;
    font-size: 18px;
    color: #333;
}

.span--arrow {
    margin-left: 10px;
    font-size: 12px;
}

.main-container.active {
    background: rgba(255, 255, 255, 0.3);
    cursor: grabbing;
    cursor: -webkit-grabbing;
    transform: scale(1);
}

input[id='0'] {
    z-index: 1000;
}

.fa-plus,
.fa-angle-down,
.fa-angle-up {
    font-size: 20px !important;
}

.button-wrapper {
    position: sticky;
    left: 90%;
}

.accordion span {
    word-wrap: break-word;
    width: 50%;
    text-align: left;
}

.positioned-button {
    margin-right: 20px;
    margin-bottom: 20px;
    float: right;
}

.ascending::after {
    content: '🔽';
    display: inline-block;
    margin-left: 1em;
}

.descending::after {
    content: '🔼';
    display: inline-block;
    margin-left: 1em;
}

/* ********************** Status Buttons *********************** */

.status-wrapper {
    position: sticky;
    left: 82%;
    display: flex;
    gap: 10px;
}

.status-button {
    width: 30px;
    height: 25px;
    font-size: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #000;
    transition: transform 0.3s ease;
}

.status-button:hover {
    transform: translateY(-1px);
}

.status-button:active {
    transform: translateY(0);
}

.status-await {
    background-color: #e8e9a1;
}

.status-approved {
    background-color: #c6ebc9;
}

/* ********************************* TOOLTIP ******************************** */

[data-tooltip]:before {
    content: attr(data-tooltip);
    position: absolute;
    opacity: 0;
    transition: all 0.2s ease;
    padding: 10px;
    color: #333;
    border-radius: 10px;
    box-shadow: 2px 2px 1px silver;
}

[data-tooltip]:hover:before {
    opacity: 1;
    margin-top: -50px;
    margin-left: 20px;
}

.status-await[data-tooltip]:hover:before {
    background: #e8e9a1;
}
.status-approved[data-tooltip]:hover:before {
    background: #c6ebc9;
}

[data-tooltip]:not([data-tooltip-persistent]):before {
    pointer-events: none;
}

textarea {
    resize: none;
}
